/* start */

.container {
  width: 1100px;
  margin-left: auto;
  margin-right: auto; }

.container-fluid {
  width: 100%; }


.panel {
  margin: 20px auto;
  padding: 10px;
  border: 1px solid #555;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: #444 !important; 
  box-shadow: 0 0 20px rgba(0,0,0,0.6);
}

.center {
  text-align: center; }

.caption {
  color: #ffffff;
  font-size: 28px;
  font-family: 'Jura', sans-serif;
  font-weight: bold;
  text-transform: uppercase; }

.label {
  color: #ffffff;
  font-size: 20px;
  font-family: 'Jura', sans-serif;
  text-transform: uppercase; }

.panel {
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.6); }

.price {
  color: #7000cc;
  font-size: 48px;
  font-family: 'Jura', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.5); }

.btn {
  display: inline-block;
  background-color: #7000cc;
  border: 2px solid #7000cc;
  border-radius: 5px;
  padding: 25px 30px;
  margin: 10px;
  color: #ffffff;
  font-size: 14px;
  font-family: 'Jura', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transtion: all 0.5s;
  -o-transition: all 0.5s; }

.btn:hover {
  background-color: #5e04a8; }

.btn.btn-secondary {
  color: #7000cc!important;
  background-color: #ffffff!important;
}

.btn.btn-secondary:hover {
  background-color: #cccccc!important;
}

.inp {
  height: 54px!important;
  border: 3px groove #999!important;
  border-radius: 5px!important;
