  border-color: #7000cc!important; }

.select-box {
  background-color: #393939!important;
  color: #999!important;
  padding: 25px 30px!important;
}

.select-box * {
  background-color: #393939!important;
  color: #999!important;
}

.ranger:focus {
  box-shadow: none!important;
}

.image-select {
  padding: 10px!important;
}

.image-select .image-select-container {
  display: inline-flex!important;
  flex-direction: row!important;
  justify-content: space-around!important;
  align-items: center!important;
}

.image-select-container .image-select-item {
  display: inline-flex!important;
  flex-direction: column!important;
  justify-content: space-around!important;
  align-items: center;
}

.image-select-item .image-select-item-image {
  width: 260px!important;
  box-sizing: border-box!important;
  transition: all .6s;
  -webkit-transition: all .6s;
  -moz-transition: all .6s;
  -o-transition: all .6s;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.image-select-item .image-select-item-image.selected {
  -webkit-filter: grayscale(0%)!important;
          filter: grayscale(0%)!important;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.image-select-item .image-select-item-image:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.image-select-item .image-select-item-text {
  font-family: 'Jura', sans-serif!important;
  font-size: 22px!important;
  color: #aaaaaa!important;
}

