}

.simple-link {
  color: rgba(46,255,175);
  text-decoration: underline;
}

.simple-link:hover {
  color: #ffffff;
}

/* Таймер */
.timer {
  width: 100%!important;
  padding: 10px!important;
}

.timer-text {
  font-size: 56px!important;
  font-family: 'Play', sans-serif!important;
  color: #ffffff!important;
  font-weight: bold!important;
}

/* информационное табло */
.info-box {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

  .info-box .info-box-label {
    padding: 10px;
  }

  .info-box .info-box-label-text {
    color: #aaaaaa;
    font-size: 22px;
    font-family: 'Play', sans-serif;
  }

  .info-box .info-box-value {
    padding: 10px;
  }

  .info-box .info-box-value-text {
    color: #ffffff;
    font-size: 54px;
    font-family: 'Play', sans-serif; 
  }

.training-day-item {
  padding: 5px!important;
  border-width: 2px;
  border-style: solid;
  border-color: #ffffff;
  border-radius: 10px;
  background-color: #333;
  width: 150px;
  height: 150px;
  box-shadow: 0 0 10px rgba(0,0,0,0.6);
}

.training-day-item:hover {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
