    -o-transform: scale(1.1);

    box-shadow: 0 0 20px rgba(46,255,175,0.6);
  }

  .training-box-container > .training-box {
    display: flex;
    position: relative;
    justify-content: space-between;
    min-height: 160px;
  }

  .training-box-container a.training-box-close {
    position: absolute;
    right: 15px;
    top: 0;
    color: #ffffff;
    font-size: 42px;
    cursor: pointer;

    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -o-transition: all .5s;
  }

  .training-box-container a.training-box-close:hover {
    color: #7000cc;
  }

    .training-box > .training-box-avatar {
      background-color: #333;
      min-height: inherit;
      margin-right: 10px;
    }

    .training-box > .training-box-avatar > .training-box-avatar-img {
      width: 300px;
      height: 300px;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      border: 4px solid #cccccc;
      box-sizing: border-box;
      border-radius: 5px;
    }

    .training-box > .training-box-info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      min-height: inherit;
      flex-grow: 1;
    }

       .training-box-info > .training-box-info-title {
         align-self: flex-start;
         padding: 0;
         margin: 0;
         font-size: 24px;
         color: #ffffff;
         font-family: 'Jura', sans-serif;
         text-transform: uppercase;
         flex-grow: 1;
       }

       .training-box-info > .training-box-info-type {
        align-self: flex-start;
        padding: 0;
        margin: 0;
        font-size: 18px;
        color: #cccccc;
        font-family: 'Jura', sans-serif;
        text-transform: uppercase;
        flex-grow: 1;
      }

      .training-box-info > .training-box-info-params {
        align-self: flex-start;
        font-size: 18px;
        color: #ffffff;
        font-family: 'Jura', sans-serif;
        flex-grow: 1;
      }

      .training-box-info > .training-box-info-params > p {
        text-align: left;
      }

      .training-box-info > .training-box-info-tools {
        align-self: flex-end;
        flex-grow: 1;
      }

